import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-form';

const Form = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

Form.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

Form.defaultProps = {
  tag: 'form'
};

Form.displayName = 'Form';

export default Form;
