import React, { forwardRef } from 'react';
import { bool, element, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-form';

const FormControl = forwardRef(({
  children,
  className,
  divide,
  resize,
  space,
  ...remainingProps
}, ref) => (
  <div
    className={classnames(`${CSS_PREFIX}-control`, {
      [`${CSS_PREFIX}-control--${divide}${resize ? '--resize' : ''}`]: Boolean(divide),
      [`${CSS_PREFIX}-control--${space}`]: Boolean(space),
      [`vds-input--split-field`]: Boolean(space)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

FormControl.propTypes = {
  /**
  * @ignore
  */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * Divide by
   */
  divide: oneOf(['twenty-five', 'fifty']),

  /**
   * Position
   */
  position: oneOf(['left', 'right']),

  /**
   * Resize
   */
  resize: bool,
  /**
   * Space
   */
  space: oneOf(['thirty', 'forty', 'fifty', 'sixty', 'seventy'])
};

FormControl.displayName = 'FormControl';

export default FormControl;
